.hero{
    display: flex;
    justify-content: space-between;
}
.left-h{
    padding:2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
    
}
.the-best-ad{
    position: relative;
    margin-top:4rem;
    background-color:#363d42 ;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color:white;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.the-best-ad>span{
    z-index:2;
}
.ss{
    position:absolute;
    background-color: var(--orange);
    width:5.4rem;
    height: 80%;
    left:8px;
    border-radius:3rem;
    z-index: 1;
}
.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform:uppercase;
    font-size:4.5rem;
    font-weight:bold;
    color:wheat;
    text-overflow: inherit;
}
.hero-text>div:nth-of-type(3){
    font-size:1rem;
    font-weight:200;
    text-transform: none;
    letter-spacing:1px;
    width: 80%;

}
.right-h{
    flex: 1;
    position: relative;
    background-color: var(--orange);
}
.figures{
    display: flex;
    gap: 2rem;
}
.figures>div{
    display: flex;
    flex-direction: column;
}
.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}
.figures>div>span:nth-of-type(2){
    color: var(--gray);
    text-transform:uppercase ;
}
.hero-buttons{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}
.hero-buttons>:nth-of-type(1){
    color:white;
    background-color: var(--orange);
    width: 8rem;
}
.hero-buttons>:nth-of-type(2){
    color:white;
    background-color: transparent;
    width: 8rem;
    border: 2px solid var(--orange);
}
.right-h>.btn{
    position:absolute;
    color:black;
    top: 2rem;
    right: 3rem;
}
.heart-rate{
    display:flex;
    flex-direction:column;
    gap:0.5rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items:start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}
.heart-rate>img{
    width: 2rem;
}
.heart-rate>:nth-child(2){
    color:var(--gray)
}
.heart-rate>:nth-child(3){
    color: white;
    font-size: 1.5rem;
}
.hero-image{
    position: absolute;
    top:10rem;
    right: 8rem;
    width: 23rem;
}
.hero-back-image{
    position: absolute;
    top:4rem;
    right: 20rem;
    z-index: -1;
    width: 15rem;
}
.calories{
    display: flex;
    gap:2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top:33rem;
    right: 28rem;
}
.calories>img{
    width: 3rem;
}
.calories>div{
    width: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   
}
.calories>div>:nth-child(1){
    color: var(--gray);
}
.calories>div>:nth-child(2){
    color: white;
    font-size: 1.5rem;
}
.hero-blur{
    width: 22rem;
    height: 38rem;
    left: 0;
}
@media screen and (max-width: 768px) {
    .hero{flex-direction: column;}
    .hero-blur{ width: 14rem;}
    
}
.the-best-ad{
   margin-top: 0;
   font-size: small;
   align-self: center;
   transform: scale(0.79);
   width: 15rem;
}
.hero-text{
    font-size: xx-large;
    align-items:center;
    justify-content: center;
}
.hero-text>div:nth-of-type(3){
    font-size: small;
    font-weight: 200;
    letter-spacing: 1px;
    text-align: center;
}
.hero-buttons{
    justify-content:center;
    font-size: small;
}
.figures>div>span:nth-of-type(1){
    font-size: large;
}
.figures>div>span:nth-of-type(2){
    font-size: small;
}
.right-h{
    position: relative;
    background: none;
}
.heart-rate{
    left: 1rem;
    top: 2rem;
    font-size: 1rem;
    background: transparent;
    border: 1px solid #363d42;
}
.calories{
    position: relative;
    top: 5rem;
    left: 1rem;
}
.calories>img{
    width: 2rem;
}
.calories>div>:nth-child(2){
    color: white;
    font-size: 1rem;
}
.hero-image{
    position: relative;
    width: 13rem;
    left: 7rem;
    top: 4rem;
    align-self: center;
}
.hero-back-image{
    width: 15rem;
    left: 2rem;
    top: 0rem;
}