.Programs{
    display: flex;
    gap: 2rem;
    flex-direction: column;
    padding: 0 2rem;
}
.programs-header{
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    text-transform: uppercase;
    color: white;
    font-style: italic;
}
.program-categories{
    display: flex;
    gap: 1rem;
}
.category{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  color: white;
    background-color: gray;
    gap: 2rem;
    padding: 1rem;

}
.join-us{
    display: flex;
  gap: 1rem;
   
}
.join-us>img{
    width: 1rem;
}
.category>:nth-child(1){
    width: 2rem;
    height: 2rem;
    fill: white;
}
.category>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}
.category>:nth-child(3){
    font-size:0.9;
    line-height: 28px;
}
.category:hover{
    background: var(--planCard);
    cursor: pointer;
}
@media screen and (max-width : 768px){
    .programs-header{
        flex-direction: column;
        gap: 1rem;
        font-size:x-large;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
    }
    .program-categories{
        flex-direction: column;
    }
}