.Reasons{
padding:0 2rem;
display:flex;
grid-template:2rem;
}
.left-r{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:1rem;
    grid-auto-rows:1fr;

}
.left-r>img{
object-fit:cover;
}
.left-r>:nth-child(1){
    width: 12rem;
    grid-row: 1/3;
    height:28rem;
}
.left-r>:nth-child(2){
    width: auto;
    grid-row: 1/2;
    height:16rem;
}
.left-r>:nth-child(3){
    width: 14rem;
    grid-row: 2/2;
   
}
.left-r>:nth-child(4){
    width: 10rem;
    grid-row: 2/3;
    grid-column:2/3;
    height: 11rem;
}
.imge-box{
flex: 1 1;
display: grid;
grid-template-columns: repeat(2,1fr);
gap:1rem;
}
.imge-box>:nth-child(1){
    width: 14rem;
    
}
.imge-box>:nth-child(2){
    width: 10rem;
    
    grid-column:2/3;
    height: 11rem;
}
.right-r{
flex: 1 1;
display: flex;
flex-direction: column;
gap: 1rem;
text-transform: uppercase;
}
.right-r>span{
    color: var(--orange);
    font-weight: bold;
}
.right-r>div{
    color: white;
font-size: 3rem;
}
.details-r{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.details-r>div{
    display: flex;
    gap: 1rem;
    font-size: 1rem;
}
.details-r>div>img{
    height: 2rem;
    width:2rem;
}
.partners{
    display: flex;
    gap:1rem;
}

.partners>.part{
    width: 2.5rem;
}
@media screen and (max-width : 768px){
    .Reasons{
        flex-direction: column;
    }
    .left-r{
        grid-auto-rows: auto;
        overflow: hidden;
    }
    .left-r>:nth-child(1){
        height: 19rem;
    width: 7rem;
      
    }
    .left-r>:nth-child(2){
        height: 10rem;
        width: 7rem;
      
    }
    .left-r>:nth-child(3){
        height: 19rem;
        width: 10rem;
      
    }
    .left-r>:nth-child(4){
        height: 19rem;
        width: 19rem;
      
    }
    .right-r{
        justify-content: center;
    }
    .right-r>div{
        color: white;
    font-size: 2rem;
    }
    .partners{
       justify-content: center;
    }
    .partner-head{
        text-align: center;
    }
}
