.header{
    display: flex;
    justify-content:space-between ;
}
.logo{
    width: 6rem;
    height: 4rem;
}
.header-menu{
    list-style: none;
    display: flex;
    gap: 3rem;
    
}
.header-menu>li:hover{
    cursor:pointer;
    color: var(--orange);
}
@media screen and (max-width: 768px) {
   .header>:nth-child(2){
    position: fixed;
    right: 2rem;
    z-index:99;
   }
   .logo{
    width: 3rem;
    height: 2rem;
   }
   .header-menu{
    flex-direction: column;
    background-color: var(--appColor);
    padding: 2rem;
    gap: 2rem;
   }
    
}